/* This example requires Tailwind CSS v2.0+ */
import { Fragment, useState, useEffect } from 'react';
import { Listbox, Transition } from '@headlessui/react';
import { CheckIcon, SelectorIcon } from '@heroicons/react/solid';

function classNames(...classes) {
	return classes.filter(Boolean).join(' ');
}

const Dropdown = ({ setResults, results }) => {
	const [selected, setSelected] = useState(results.Global);

	useEffect(() => {
		setResults(selected);
	}, [selected]);

	return (
		<Listbox value={selected} onChange={setSelected}>
			{({ open }) => (
				<>
					<Listbox.Label className='block text-2xl font-medium text-gray-700'>
						Countries
					</Listbox.Label>
					<div className=' ml-10 mr-10  mt-1 relative'>
						<Listbox.Button className='w-full bg-white border border-gray-300 rounded-md shadow-sm pl-3 pr-10 py-3 p-10 text-center cursor-default focus:outline-none focus:ring-1 focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm'>
							<span className='flex items-center text-xl'>
								<span className='ml-3 block truncate'>
									{selected?.Country ? selected?.Country : 'Global'}
								</span>
							</span>
							<span className='ml-3 absolute inset-y-0 right-0 flex items-center pr-2 pointer-events-none'>
								<SelectorIcon
									className='h-5 w-5 text-gray-400'
									aria-hidden='true'
								/>
							</span>
						</Listbox.Button>

						<Transition
							show={open}
							as={Fragment}
							leave='transition ease-in duration-100'
							leaveFrom='opacity-100'
							leaveTo='opacity-0'>
							<Listbox.Options
								static
								className='absolute z-10 mt-1 w-full bg-white shadow-lg max-h-56 rounded-md py-1 text-base ring-1 ring-black ring-opacity-5 overflow-auto focus:outline-none sm:text-sm'>
								{results?.Countries.map((country) => (
									<Listbox.Option
										key={country.ID}
										className={({ active }) =>
											classNames(
												active ? 'text-white bg-indigo-600' : 'text-gray-900',
												'cursor-default select-none relative py-2 pl-3 pr-9'
											)
										}
										value={country}>
										{({ selected, active }) => (
											<>
												<div className='flex items-center text-xl'>
													<span
														className={classNames(
															selected ? 'font-semibold' : 'font-normal',
															'ml-3 block truncate'
														)}>
														{country.Country}
													</span>
												</div>

												{selected ? (
													<span
														className={classNames(
															active ? 'text-white' : 'text-indigo-600',
															'absolute inset-y-0 right-0 flex items-center pr-4'
														)}>
														<CheckIcon className='h-5 w-5' aria-hidden='true' />
													</span>
												) : null}
											</>
										)}
									</Listbox.Option>
								))}
							</Listbox.Options>
						</Transition>
					</div>
				</>
			)}
		</Listbox>
	);
};

export default Dropdown;
